        border-color: $btn-danger !important;
    }
    .has-info & {
      border-color: $btn-info !important;
    }
    .has-warning & {
      border-color: $btn-warning !important;
    }
    .has-success & {
      border-color: $btn-success !important;
    }
 }
*/


}//@if $plugin-chosen