html {
  --navbar-height: 4.5rem;
  --navbar-sm-height: 4rem;
  --navbar-mobile-height: 4rem;
  --sidebar-width: 16rem;
  --sidebar-collapsed-width: 5rem;
  --sidebar-horizontal-height: 4.25rem;
  --sidebar-horizontal-collapsed-height: 4rem;
  direction: ltr;
  text-align: left;
}

html, body {
  height: 100%;
  font-family: "Open Sans", Arial, sans-serif;
}

body {
  overflow-x: hidden;
  background-color: #e4e6e9;
  color: #484b51;
}

.body-container {
  background-color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.body-container.container {
  padding: 0;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.05);
}

.body-container:not(.container) {
  max-width: 100vw;
}

.main-container {
  flex: 1 0 auto;
  display: flex;
  position: relative;
  max-width: 100vw;
}

.main-content {
  max-width: 100%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

@media (min-width: 1220px) {
  .container {
    max-width: calc(100vw - 4rem);
  }
}

@media (min-width: 1440px) {
  .container {
    max-width: 1408px;
  }
}

@media (min-width: 1600px) {
  .container.container-plus {
    max-width: 1536px;
  }
}

@media (max-width: 1199.98px) {
  body {
    background-color: #fff;
  }
  .container {
    max-width: none;
  }
  .body-container {
    max-width: 100vw;
    overflow: hidden;
  }
}

.page-content {
  flex: 1 0 auto;
  padding: 1rem 1rem 0.625rem;
}

@media (min-width: 768px) {
  .page-content {
    padding: 1.25rem 1.25rem 0.625rem;
  }
}

@media (min-width: 1200px) {
  .page-content {
    padding: 1.25rem 1.5rem 0.625rem;
  }
}

.page-header {
  margin: 0 0 1rem;
  padding-bottom: 1rem;
  padding-top: 0.5rem;
  border-bottom: 1px dotted #e2e2e2;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-header .page-tools {
  align-self: flex-end;
}

.page-title {
  padding: 0;
  margin: 0;
  font-size: 1.75rem;
  font-weight: 300;
}

.page-title .page-info {
  margin: 0 0.25rem;
  font-size: 1.0625rem;
  font-weight: 400;
  display: block;
}

@media (min-width: 768px) {
  .page-header .page-info {
    display: inline-block;
  }
}

